.content__places {
    margin: 50px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, 282px);
    column-gap: 17px;
    row-gap: 20px;
    justify-content: center;
}

@media screen and (max-width: 720px) {
    .content__places {
        margin: 0 auto 48px;
       }
}